import {
  NavItem,
  Navbar,
  Icon,
  Button,
  Col,
  CardTitle,
  Card,
  Row,
  CardPanel,
  MediaBox,
  Divider,
  Modal,
  DatePicker,
  Form,
  Carousel,
  Collapsible,
  CollapsibleItem,
} from "react-materialize";
import "materialize-css";
import YouTube from "react-youtube";
import React, { Component } from "react";
import ListaWzor from "./ListaWzor";
import PicturesGallery from "./PicturesGallery";

class Galeria extends Component {
  constructor() {
    super();

    this.state = {
      search: null,
      site: "Lake",
      size: "140m2",
      roof: "Blacha",
      wall: "Aluminium",
      view: "Front",
      scenery: "Summer",

      idtoshow:
        "https://raw.githubusercontent.com/galeriapodsloncem/3dstrona/master/babylonjs-app/public/assets/zdjeciakonfigurator/Lake140m2BlachaAluminiumSummerFront.png",
      www:
        "https://raw.githubusercontent.com/galeriapodsloncem/3dstrona/master/babylonjs-app/public/assets/zdjeciakonfigurator/",
      extension: ".png",
    };
  }

  OnClinkSearchSize = (event, id) => {
    this.setState({ size: id });
    this.setState({
      idtoshow:
        this.state.www +
        this.state.site +
        this.state.size +
        this.state.roof +
        this.state.wall +
        this.state.scenery +
        this.state.view +
        this.state.extension,
    });
    window.scrollTo(0, 0);
  };
  sitePick = (event, id) => {
    this.setState({ site: id });
    this.setState({
      idtoshow:
        this.state.www +
        this.state.site +
        this.state.size +
        this.state.roof +
        this.state.wall +
        this.state.scenery +
        this.state.view +
        this.state.extension,
    });
    window.scrollTo(0, 0);
  };
  OnClinkSearchWall = (event, id) => {
    this.setState({ wall: id });
    this.setState({
      idtoshow:
        this.state.www +
        this.state.site +
        this.state.size +
        this.state.roof +
        this.state.wall +
        this.state.scenery +
        this.state.view +
        this.state.extension,
    });
    window.scrollTo(0, 0);
  };
  OnClinkSearchRoof = (event, id) => {
    this.setState({ roof: id });
    this.setState({
      idtoshow:
        this.state.www +
        this.state.site +
        this.state.size +
        this.state.roof +
        this.state.wall +
        this.state.scenery +
        this.state.view +
        this.state.extension,
    });
    window.scrollTo(0, 0);
  };
  OnClinkSearchView = (event, id) => {
    this.setState({ view: id });
    this.setState({
      idtoshow:
        this.state.www +
        this.state.site +
        this.state.size +
        this.state.roof +
        this.state.wall +
        this.state.scenery +
        this.state.view +
        this.state.extension,
    });
    window.scrollTo(0, 0);
  };
  OnClinkSearchScenery = (event, id) => {
    this.setState({ scenery: id });
    this.setState({
      idtoshow:
        this.state.www +
        this.state.site +
        this.state.size +
        this.state.roof +
        this.state.wall +
        this.state.scenery +
        this.state.view +
        this.state.extension,
    });
    window.scrollTo(0, 0);
  };

  render() {
    return (
      <div className="galeria">
        <Row>
          <Carousel
            carouselId="Carousel-2"
            images={[
              PicturesGallery.img1,
              PicturesGallery.img2,
              PicturesGallery.img3,
              PicturesGallery.img4,
              PicturesGallery.img5,
              PicturesGallery.img6,
              PicturesGallery.img7,
              PicturesGallery.img8,
              PicturesGallery.img9,
              PicturesGallery.img10,
              PicturesGallery.img11,
            ]}
            options={{
              fullWidth: true,
              indicators: true,
              duration: 300,
              numVisible: true,
            }}
          />
        </Row>
      </div>
    );
  }
}

export default Galeria;
